import React from "react";
import './index.less'
import ReactDOM from "react-dom";
import App from "../../App";

export  const Tips = (text,time,bgc) =>{





    const ulNode = document.getElementById('root'),
        liNode = document.createElement('div');

    liNode.className = `tips ${bgc}`;
    //liNode.style = {'background':'#456787'};
    liNode.innerHTML = text;

    ulNode.appendChild(liNode);
    setTimeout(()=>ulNode.removeChild(liNode)
,time)




    //ReactDOM.render(<Modle/>, document.getElementById('tips'))
    //
    // setTimeout(()=>ReactDOM.unmountComponentAtNode(document.getElementById(keys)),time)



}